.sync1 {
  .item {
    color: $white-2;
  }
}

.sync2 {
  .item {
    padding: 10px 0px;
    margin: 5px;
    color: $white-2;
    text-align: center;
    cursor: pointer;
  }
}

.owl-theme {
  .owl-nav {
    [class*='owl-'] {
      transition: all .3s ease;

      &.disabled:hover {
       background-color: #d6d6d6;
      }   
    } 
  }
}

//arrows on first carousel
.sync1.owl-theme {
  position: relative;

  &:hover {
    .owl-next, .owl-prev {
      display: inline-block;
    }
  }

  .owl-next, .owl-prev {
    display: none;
    width: 22px;
    height: 40px;
    position: absolute;
    bottom: 50%;
    transform: translateY(-50%);
  }

  .owl-prev {
    left: 10px;
  }

  .owl-next {
    right: 10px;
  }
}