.cases {
  display: flex;
  flex-direction: column;
  perspective: 1000px;
  transform-style: preserve-3d;

  .title {
    font-size: 36px / $font_default * 1em;
    font-weight: bold;
    letter-spacing: 3px;
    line-height: 1.3;
    text-align: left;
    transform: translateZ(20px);
  }
}

.case-wrapper {
  display: flex;
  justify-content: center;
  width: 100%;
}

.case {
  backface-visibility: hidden;
  display: flex;
  justify-content: center;
  margin-bottom: 133px;
  max-width: 891px;
  position: relative;
  transform: translateZ(0);
  transform-style: preserve-3d;
  width: 80%;

  button {
    border: 0;
    cursor: pointer;
    height: 56px;
    letter-spacing: 3.1px;
    text-transform: uppercase;
    transform: translateZ(20px);
    width: 188px;
  }

  img {
    display: none;
    position: absolute;
  }

  .container {
    transform-style: preserve-3d;
    width: 85%;
  }
}

.hashtags {
  display: flex;
  flex-wrap: wrap;
  margin: 28px 0;
}

.hashtag {
  color: $white;
  letter-spacing: 1.3px;
  line-height: 1.5;
  margin-right: 11px;
  text-align: left;
  transform: translateZ(15px);
}

.case.nerdwallet {
  background-image: none;
  height: 409px;
  padding: 90px;
  padding-left: 0;
  padding-right: 0;
  padding-top: 68px;

  button {
    background-color: $sienna;
  }
}

.case.gingerio-relief {
  background-image: none;
  height: 415px;
  padding-left: 0;
  padding-top: 87px;

  button {
    background-color: $bismark;
  }
}

.case.mood {
  background-image: none;
  height: 436px;
  padding-left: 0;
  padding-top: 83px;

  button {
    background-color: $tabasco;
  }
}

@media screen and (min-width: 951px) {
  .case {
    display: block;

    .container {
      width: 100%;
    }

    img {
      display: inline-block;
    }
  }

  .case.nerdwallet {
    padding-left: 11.7%;

    img {
      right: 0;
      top: 6px;
    }

    button {
      background-color: #6e728e;
    }
  }

  .case.gingerio-relief {
    height: 415px;
    padding-top: 87px;

    .container {
      align-items: center;
      display: flex;
      flex-direction: column;
    }

    img {
      left: -17%;
      bottom: -86%;
    }

    a {
      align-self: flex-end;
      margin-right: 17%;
    }

    button {
      background-color: #c5796b;
    }
  }

  .case.mood {
    height: 436px;
    padding-left: 5.6%;
    padding-top: 83px;

    img {
      right: 7%;
      top: 6px;
    }

    button {
      background-color: #6c9477;
    }
  }
}


@media screen and (min-width: 1336px) {
  .case.nerdwallet { 
    padding-left: 92px;
  }

  .case.mood {
    padding-left: 83px;
  }
}

@media screen and (min-width: 651px) {
  .case.nerdwallet {
    background-image: url('/images/cases/nerdwallet.svg');
  }

  .case.gingerio-relief {
    background-image: url('/images/cases/relief.svg');
  }

  .case.mood {
    background-image: url('/images/cases/tracking.svg');
  }
}
